import React from 'react';

const Contact = () => {
  return (
    <section id="contact" style={{ padding: '2rem', fontFamily: 'Arial, sans-serif' }}>
      <h2>Contact Us</h2>
      
      <div style={{ marginBottom: '1.5rem' }}>
        <h3>General Inquiries</h3>
        <p>
          <a href="mailto:contact@innotexon.com">contact@innotexon.com</a>
        </p>
      </div>

      <div style={{ marginBottom: '1.5rem' }}>
        <h3>Support</h3>
        <p>
          <a href="mailto:support@innotexon.com">support@innotexon.com</a>
        </p>
      </div>

      <div style={{ marginBottom: '1.5rem' }}>
        <h3>Operations</h3>
        <p>
          <a href="mailto:ops@innotexon.com">ops@innotexon.com</a>
        </p>
      </div>

      <div style={{ marginBottom: '1.5rem' }}>
        <h3>Research Labs</h3>
        <p>
          <a href="mailto:labs@innotexon.com">labs@innotexon.com</a>
        </p>
      </div>

      <div>
        <h3>Legal</h3>
        <p>
          <a href="mailto:legal@innotexon.com">legal@innotexon.com</a>
        </p>
      </div>
    </section>
  );
};

export default Contact;
