import React from 'react';
import logo from '../assets/origianllogo.jpg'; // Adjust path if needed

const Hero = () => {
  return (
    <section className="hero-section" style={{ padding: '4rem 2rem', textAlign: 'center' }}>
      <div className="hero-logo">
        <img src={logo} alt="Innotexon Logo" className="logo" />
      </div>
      <h1>Welcome to Innotexon</h1>
      <p>Innovating the future, one breakthrough at a time.</p>
    </section>
  );
};

export default Hero;
